.header {
  padding: 20px 0;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: block;
    width: 200px;
    height: 42px;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__contact {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__call {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 62px;
    height: 62px;
    border-radius: 10px;
    background-color: var(--accent);
    padding: 13.5px;
    svg {
      width: 100%;
      height: 100%;
    }
  }
}
