.benefits {
  padding: 40px 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  &__item {
    padding: 30px;
    min-height: 580px;
    border: 1px solid var(--seraya-obvodka);
    border-radius: 20px;
    box-shadow: 0 0 20px 0 rgba(156, 156, 156, 0.1);
    background: linear-gradient(
      135deg,
      rgba(233, 242, 253, 0.4) 0%,
      rgba(249, 241, 238, 0.4) 100%
    );
  }

  &__image {
    height: 280px;
    margin-bottom: 20px;
    overflow: hidden;
    border-radius: 15px;
    img {
      width: 100%;
      height: 100%;
    }
  }

  &__capture {
    margin-bottom: 13px;
  }
}
